}

.ccSpaceFlowList{
    ul{
        list-style:none;
        li{
            padding:30px 0;
            a{
                text-decoration:none;
                color:#0B4D8A;
                font-size:4em;
                font-weight:700;
                position:relative;
                display:block;
                z-index:11;
                &:before{
                    content: '';
                    background: #F9B131;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 0;
                    z-index: -1;
                    transition:all .25s ease-in-out;
                }
                &:hover{
                    &:before{
                        height:30px;
                    }
                }
            }
        }
    }
}

.ccspaceFormHeadCol{
	height: 100vh;
    display: flex;
    justify-content: center;
    align-items: end;
    flex-direction: column;
    font-size:3em;
    color:#F9B131;
}
.ccspaceFormCol{
	height: 100vh;
    display: flex;
    justify-content: center;
    align-items: end;
    flex-direction: column;
    position:relative;
    // padding-left: 10%;
    &:before{
    	content:'';
